.symbolsIcon {
	position: absolute;
	height: 96.97%;
	width: 99.82%;
	top: 1.59%;
	right: 0.18%;
	bottom: 1.44%;
	left: 0;
	max-width: 100%;
	overflow: hidden;
	max-height: 100%;
}
.fickleflightLogo {
	border: 0;
	padding: 0;
	background-color: transparent;
	box-shadow: 0 0 0 4px rgba(255, 255, 255, 0.01);
	width: 174px;
	height: 33px;
}
.explore,
.fickleflightLogo,
.offers,
.search {
	position: relative;
	cursor: pointer;
}
.offers,
.search {
	border: 0;
	padding: 0;
	background-color: transparent;
	font-size: var(--font-size-sm);
	font-family: var(--components-button-large);
	color: var(--color-cornflowerblue-100);
	text-align: left;
	display: inline-block;
	text-shadow: 0 0 0 rgba(255, 255, 255, 0.01);
}
.offers {
	color: var(--color-darkslategray-100);
}
.navigationMenu {
	box-shadow: 0 0 0 4px rgba(255, 255, 255, 0.01);
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-end;
	gap: var(--gap-11xl);
}
.hamburgerMenuIcon {
	position: relative;
	width: 24px;
	height: 24px;
	overflow: hidden;
	flex-shrink: 0;
	display: none;
}
.notificationBellIcon {
	position: relative;
	width: 24.46px;
	height: 25.63px;
}
.unsplashd1upkifd04aIcon {
	border-radius: var(--br-19xl);
	width: 40px;
	height: 40px;
	object-fit: cover;
}
.accountSection {
	box-shadow: 0 0 0 4px rgba(255, 255, 255, 0.01);
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	gap: var(--gap-xs);
}
.navigationRight {
	justify-content: flex-end;
	gap: var(--gap-17xl);
}
.navigationRight,
.topContainer,
.topHeader {
	box-shadow: 0 0 0 4px rgba(255, 255, 255, 0.01);
	display: flex;
	flex-direction: row;
	align-items: center;
}
.topContainer {
	flex: 1;
	justify-content: space-between;
}
.topHeader {
	align-self: stretch;
	background-color: var(--primary-contrast);
	height: 77px;
	padding: var(--padding-3xl) var(--padding-61xl);
	box-sizing: border-box;
	justify-content: center;
	position: sticky;
	width: 100%;
	z-index: 100;
	top: 0;
	background: #fff;
	text-align: left;
	font-size: var(--font-size-sm);
	color: var(--color-darkslategray-100);
	font-family: var(--components-button-large);
}
@media screen and (max-width: 1200px) {
	.topHeader {
		padding-left: var(--padding-21xl);
		padding-right: var(--padding-21xl);
		box-sizing: border-box;
	}
}
@media screen and (max-width: 1100px) {
	.navigationMenu {
		display: none;
	}
	.hamburgerMenuIcon {
		display: flex;
	}
	.topHeader {
		padding-left: var(--padding-5xl);
		padding-right: var(--padding-5xl);
		box-sizing: border-box;
	}
}
@media screen and (max-width: 650px) {
	.navigationRight {
		display: flex;
	}
}
